import React from "react";
import { Layout, Row, Col, Input, Select, notification } from "antd";
import { LoggedinHeader } from "../../containers";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { AudioOutlined } from "@ant-design/icons";
import HotlinePopover from "../table/HotlinePopover";
import { Link } from "react-router-dom";
import PermissionModal from "./../modal/ShowModal";
import OrgSidebar from "../../containers/OrgSidebar";
import moment from "moment";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import authHeader from "../../services/auth-header";
import * as AppStorage from "../../utils/AppStorage";

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);
const { Content } = Layout;

class ShowAllHotlines extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      filterData: [],
      searchBy: "",
    };
  }

  componentDidMount() {
    let { hotlines } = this.props;
    hotlines.forEach((abc) => {
      abc.start_date = moment(abc.start_date)
        .utc()
        .local()
        .format("DD / MM / YYYY");
      // abc.start_date = abc.start_date.replaceAll("T00:00:00Z", "");
      abc.end_date = moment(abc.end_date)
        .utc()
        .local()
        .format("DD / MM / YYYY");
      // abc.end_date = abc.end_date.replaceAll("T00:00:00Z", "");
    });
    this.setState({ data: hotlines }, () => {
      this.onSearch("");
    });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.hotlines !== nextProps.hotlines) {
      nextProps.hotlines.forEach((abc) => {
        abc.start_date = moment(abc.start_date)
          .utc()
          .local()
          .format("DD / MM / YYYY");
        abc.end_date = moment(abc.end_date)
          .utc()
          .local()
          .format("DD / MM / YYYY");
      });
      this.setState({ data: nextProps.hotlines }, () => {
        this.onSearch(this.state.searchBy);
      });
    }
  }

  deleteHandle = (name) => {
    notification.config({
      placement: "bottomLeft",
    });
    notification.open({
      description: `${name} has been deleted successfully.`,
    });
    window.location = "/orgcc/hotlinedirectory";
  };

  statusClicked = (record, via) => {
    const endDate = record.end_date.replaceAll(" / ", "-");
    const date = endDate.split("-")[0];
    const month = endDate.split("-")[1];
    const year = endDate.split("-")[2];
    const finalDate = month + "-" + date + "-" + year;
    if (new Date() < new Date(finalDate)) {
      const user = JSON.parse(AppStorage.get("user"));
      const data = {
        help_text: record.help_text,
        hotline_icon_id: record.hotline_icon_id,
        hotline_id: record.hotline_id,
        name: record.name,
        start_date: record.start_date.replaceAll(" / ", "-"),
        end_date: record.end_date.replaceAll(" / ", "-"),
        status_id: via === "Active" ? 4 : 5,
        updated_by: user.user_details.org_user_id,
      };
      const token = authHeader();
      const options = {
        headers: {
          Authorization: token,
          Accept: "application/json, text/plain",
          "Content-Type": "application/json;charset=UTF-8",
        },
      };
      axios
        .put(BASE_URL + "hotline/" + data.hotline_id, data, options)
        .then(() => {
          notification.config({
            placement: "bottomLeft",
          });
          notification.open({
            description: `Status of ${record.name} has been changed successfully.`,
          });
          this.props.statusOnClicked();
        });
    } else {
      notification.config({
        placement: "bottomLeft",
      });
      notification.open({
        description: `Status of ${record.name} can't be changed as the end date has been passed.`,
      });
    }
  };

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt, searchBy: value });
  };

  render() {
    const columns = [
      {
        title: "Hotline Name",
        dataIndex: "name",
        key: "name",
        width: "22%",
        render: (name) => {
          return {
            children: name,
            props: {
              "data-title": "Hotline Name",
            },
          };
        },
      },
      {
        title: "Start Date",
        dataIndex: "start_date",
        key: "start_date",
        width: "22%",
        render: (start_date) => {
          return {
            children: start_date,
            props: {
              "data-title": "Start Date",
            },
          };
        },
      },
      {
        title: "End Date",
        dataIndex: "end_date",
        key: "end_date",
        width: "21%",
        render: (end_date) => {
          return {
            children: end_date,
            props: {
              "data-title": "End Date",
            },
          };
        },
      },
      //{ title: "Permissions", dataIndex: "", key: "", width: "20%" },
      {
        title: "Permissions",
        dataIndex: "",
        key: "x",
        width: "20%",
        render: (selectedRows) => {
          return {
            children: (
              <div>
                <PermissionModal selectedRows={selectedRows} />
              </div>
            ),
            props: {
              "data-title": "Permissions",
            },
          };
        },
      },
      {
        title: "Status",
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) => {
          return {
            children:
              record.status_id === 5 ? (
                <div
                  className="primary-tag-rounded tag"
                  onClick={() => this.statusClicked(record, "Active")}
                >
                  <span className="circle mr-1"></span>Active
                </div>
              ) : (
                <div
                  className="secondary-tag-rounded tag"
                  onClick={() => this.statusClicked(record, "InActive")}
                >
                  Inactive
                </div>
              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
      {
        title: "",
        dataIndex: "",
        key: "x",
        width: "5%",
        render: (record) => {
          return {
            children: (
              <HotlinePopover data={record} deleteHandle={this.deleteHandle} />
            ),
            props: {
              "data-title": "Action",
            },
          };
        },
      },
    ];

    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={this.props.data} />
              <Content className="">
                <Row className="py-3">
                  <h1>Hotline Directory</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search
                            placeholder="Search"
                            onSearch={this.onSearch}
                          />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Link to="/orgcc/createhotline">
                        <Buttons value="Create Hotline" />
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <Grid
                  rowKey="hotline_id"
                  columns={columns}
                  data={this.state.filterData}
                />
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default ShowAllHotlines;
