import React from "react";
import { Layout, Menu, Row } from "antd";
import { Link } from "react-router-dom";
const { Sider, Header, Content } = Layout;
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  UserOutlined,
  VideoCameraOutlined,
  UploadOutlined,
} from "@ant-design/icons";

class OrgSidebar extends React.Component {
  state = {
    collapsed: false,
  };

  toggle = () => {
    console.log("this.state.collapsed", this.state.collapsed);
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    const defaultKey = this.props.data?.location?.state;
    return (
      <>
        {React.createElement(
          this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
          {
            className: "trigger d-lg-none d-block",
            onClick: this.toggle,
          }
        )}
        <Sider
          className={
            this.state.collapsed
              ? "d-none d-lg-block w-md-100 sider-width"
              : " d-lg-block w-md-100 sider-width"
          }
          trigger={null}
          collapsible
          collapsed={this.state.collapsed}
        >
          <Menu
            theme="dark"
            mode="inline"
            defaultSelectedKeys={defaultKey ? [defaultKey] : ["3"]}
          >
            <Menu.Item
              key="1"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-hotlines" />
                </span>
              }
            >
              {" "}
              <Link to={{ pathname: "/orgcc/hotlinedirectory", state: "1" }}>
                Hotlines
              </Link>
            </Menu.Item>
            <Menu.Item
              key="2"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-openlines" />
                </span>
              }
            >
              {" "}
              <Link
                to={{ pathname: "/orgcc/openlinedirectory", state: "2" }}
              ></Link>
              Openlines
            </Menu.Item>
            {/* <Menu.Item
          key="3"
          icon={
            <span className="icon-circle">
              <span className="icon icon-polls" />
            </span>
          }
        >
          {" "}
          Polls
        </Menu.Item> */}
            <Menu.Item
              key="3"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-manage-emp" />
                </span>
              }
            >
              {" "}
              <Link to={{ pathname: "/orgcc/manageemployees", state: "3" }}>
                Manage Employees
              </Link>
            </Menu.Item>
            <Menu.Item
              key="4"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-broadcast" />
                </span>
              }
            >
              {" "}
              Broadcast Messages
            </Menu.Item>
            <Menu.Item
              key="5"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-help" />
                </span>
              }
            >
              {" "}
              Help
            </Menu.Item>
            <Menu.Item
              key="6"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-info" />
                </span>
              }
            >
              {" "}
              Info
            </Menu.Item>
          </Menu>
          <div className="btm-div">
            <a href="#" className="px-2">
              Info
            </a>
            <a href="#" className="px-2">
              Help
            </a>
          </div>
        </Sider>
      </>
    );
  }
}

export default OrgSidebar;
